html {
  min-width: 100vw;
  max-width: 100vw;
  min-height: 100vh;
  font-size: 1.5vmin;
  cursor: default;
}

body {
  margin: 0;
  font-family: "Capriola", sans-serif;
  min-height: 100vh;
  min-width: 100vw;
}

h1, h2, h3, h4, h5 {
  font-family: "Baloo Bhai 2", cursive;
}

.container {
  min-width: 100vw;
  max-width: 100vw;
  display: grid;
  grid-template-columns: 1fr;
}
.container div {
  max-width: 100vw;
}

.auth-page-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: calc(100vh - 5.75rem);
}
.auth-page-wrapper .left-column {
  background-size: cover;
}
.auth-page-wrapper .right-column {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(232, 240, 254);
}
.auth-page-wrapper .right-column .auth-form-wrapper {
  display: grid;
  grid-gap: 1.3rem;
  grid-gap: 1.25rem;
  padding: 2.75rem;
}
.auth-page-wrapper .right-column .auth-form-wrapper input {
  color: rgb(66, 69, 74);
  padding: 0.3rem 0;
  width: 100%;
  border: 0;
  border-bottom: 0.1rem solid rgb(0, 146, 230);
  background-color: transparent;
  transition: 0.3s all ease-in-out;
  font-family: "Capriola", sans-serif;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}
.auth-page-wrapper .right-column .auth-form-wrapper input:focus {
  outline: none;
  border-bottom: 0.1rem solid rgb(0, 171, 194);
}
.auth-page-wrapper .right-column .auth-form-wrapper .form-group {
  display: flex;
}
.auth-page-wrapper .right-column .auth-form-wrapper .form-group input, .auth-page-wrapper .right-column .auth-form-wrapper .form-group input:-internal-autofill-selected, .auth-page-wrapper .right-column .auth-form-wrapper .form-group input:-webkit-autofill, .auth-page-wrapper .right-column .auth-form-wrapper .form-group input:autofill {
  padding: 0.4rem 1rem;
  font-family: "Capriola", sans-serif !important;
}
.auth-page-wrapper .right-column .auth-form-wrapper .form-group svg {
  font-size: 2rem;
  color: rgb(0, 146, 230);
  margin-right: 1rem;
}

textarea {
  border: 0.1rem solid rgb(0, 146, 230);
  border-radius: 0.5rem;
  background-color: transparent;
  font-family: "Capriola", sans-serif;
  font-size: 1rem;
  height: 10rem;
  outline: none;
  padding: 1rem;
}

.select-element {
  border: 0.1rem solid rgb(0, 146, 230);
  font-family: "Capriola", sans-serif;
  padding: 0.5rem;
  color: rgb(66, 69, 74);
  font-size: 1rem;
  width: 100%;
  border-radius: 0.5rem;
  overflow: hidden;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.dz-box {
  font-family: "Capriola", sans-serif;
  color: rgb(66, 69, 74);
}

option {
  background-color: rgb(180, 255, 218);
  color: rgb(130, 85, 255);
}
option:hover {
  cursor: pointer !important;
}

.btn {
  cursor: pointer;
  font-family: "Capriola", sans-serif;
  font-size: 1.15rem;
  font-weight: 500;
  border: 0.2rem solid rgb(0, 171, 194);
  border-radius: 0.5rem;
  transition: all 0.3s ease-in-out;
  background-color: rgb(0, 255, 213);
  color: rgb(130, 85, 255);
  padding: 0.25rem 0.5rem;
}
.btn:active, .btn:hover {
  outline: none;
  border: 0.2rem solid rgb(130, 85, 255);
  background-color: rgb(0, 225, 255);
}

.one-column {
  display: grid;
  grid-gap: 1.3rem;
  grid-template-columns: 1fr;
}

.two-column {
  display: grid;
  grid-gap: 1.3rem;
  grid-template-columns: 1fr 1fr;
}

.three-column {
  display: grid;
  grid-gap: 1.3rem;
  grid-template-columns: 1fr 1fr 1fr;
}

.nav-wrapper {
  display: flex;
  justify-content: space-between;
  margin: 2rem;
}
.nav-wrapper .right-side {
  display: flex;
  line-height: 1.5vmax;
  font-size: 1.5vmax;
}
.nav-wrapper .right-side a {
  margin-left: 1rem;
  cursor: pointer;
  color: rgb(0, 146, 230);
  transition: 0.3s ease-in-out;
}
.nav-wrapper .right-side a:hover {
  color: rgb(130, 85, 255);
}
.nav-wrapper .left-side {
  display: flex;
}
.nav-wrapper .left-side, .nav-wrapper .right-side {
  height: 1.75rem;
  max-height: 1.75rem;
  min-height: 1.75rem;
  align-items: center;
}
.nav-wrapper .nav-link-wrapper {
  margin-right: 1.25rem;
  text-transform: uppercase;
}
.nav-wrapper .nav-link-wrapper a {
  color: rgb(138, 138, 138);
  text-decoration: none;
}
.nav-wrapper .nav-link-wrapper a:hover {
  color: rgb(0, 171, 194);
}
.nav-wrapper .nav-link-wrapper .nav-link {
  font-size: 1.5vmax;
  line-height: 1.5vmax;
  border-bottom: 0.2rem solid transparent;
}
.nav-wrapper .nav-link-wrapper .nav-link:hover {
  border-bottom: 0.2rem solid rgb(0, 171, 194);
}
.nav-wrapper .nav-link-wrapper .nav-link-active {
  border-bottom: 0.2rem solid black;
  color: black;
  cursor: default;
}
.nav-wrapper .nav-link-wrapper .nav-link-active:hover {
  border-bottom: 0.2rem solid black;
  color: black;
}

.portfolio-items-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  border: 1vh solid white;
  border-top: none;
}
.portfolio-items-wrapper .portfolio-item-wrapper {
  border: 1vh solid white;
  background-color: rgb(255, 255, 255);
  position: relative;
  overflow: hidden;
}
.portfolio-items-wrapper .portfolio-item-wrapper:hover {
  cursor: pointer;
}
.portfolio-items-wrapper .portfolio-item-wrapper .portfolio-img-background {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 35vh;
  width: 100%;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  text-align: center;
  padding-left: 2.5vw;
  padding-right: 2.5vw;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .item-title-wrap {
  margin: 2rem 0 0;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .item-title-wrap .item-title {
  flex: 1;
  font-size: 2.75vmax;
  color: rgb(0, 225, 255);
  transition: 0.3s ease-in-out;
  opacity: 0%;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .logo-subtitle-wrapper {
  flex: 1;
  display: flex;
  align-items: center;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .logo-subtitle-wrapper .subtitle {
  padding-left: 1.5rem;
  transition: 0.3s ease-in-out;
  color: rgb(0, 255, 213);
  opacity: 0%;
  font-size: 1.25vmax;
  font-weight: 400;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper:hover .subtitle {
  opacity: 100%;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper:hover .item-title {
  opacity: 100%;
}
.portfolio-items-wrapper .portfolio-item-wrapper .logo-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.portfolio-items-wrapper .portfolio-item-wrapper .logo-wrapper img {
  border-radius: 1.5vw;
  padding: 0.5vw;
  background-color: rgba(139, 139, 139, 0.5);
  height: 6vw;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .item-title-wrap .touch-screen-text {
  opacity: 100%;
}
.portfolio-items-wrapper .portfolio-item-wrapper .img-text-wrapper .logo-subtitle-wrapper .touch-screen-text {
  opacity: 100%;
}

.image-blur {
  background-color: rgb(160, 160, 160);
  transition: 0.3s ease-in-out;
  filter: brightness(23.5%);
}

.filter-buttons {
  height: fit-content;
  width: calc(100vw - 4vh);
  display: flex;
  justify-content: center;
  gap: 2vh;
  margin: 0 2vh 1vh;
}
.filter-buttons .btn {
  padding: 0.5vh;
  font-size: 3vh;
  width: 100%;
  border-radius: 1.6vh;
}

.portfolio-manager-wrapper {
  display: grid;
  grid-template-columns: 3fr 1fr;
}
.portfolio-manager-wrapper .left-column {
  background-color: rgb(255, 252, 217);
}
.portfolio-manager-wrapper .right-column {
  background-color: rgb(66, 69, 74);
}

.portfolio-form-wrapper {
  display: grid;
  grid-gap: 1.3rem;
  grid-template-columns: 1fr;
  padding: 2.75rem;
}
.portfolio-form-wrapper input {
  color: rgb(66, 69, 74);
  padding: 0.3rem 0;
  width: 100%;
  border: 0;
  border-bottom: 0.1rem solid rgb(0, 146, 230);
  background-color: transparent;
  transition: 0.3s all ease-in-out;
  font-family: "Capriola", sans-serif;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}
.portfolio-form-wrapper input:focus {
  outline: none;
  border-bottom: 0.1rem solid rgb(0, 171, 194);
}
.portfolio-form-wrapper input {
  margin-bottom: 0;
}
.portfolio-form-wrapper .image-uploaders {
  display: grid;
  grid-gap: 1.3rem;
  grid-gap: 1.5rem;
  height: 11.5rem;
}
.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper .image-removal-link {
  margin: 0.5rem;
  color: rgb(66, 69, 74);
  transition: 0.3s ease-in-out;
}
.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper .image-removal-link:hover {
  color: rgb(255, 91, 219);
  cursor: pointer;
}
.portfolio-form-wrapper .image-uploaders .portfolio-manager-image-wrapper img {
  width: 100%;
  height: 10rem;
  object-fit: cover;
  border: 0.1rem solid transparent;
  border-radius: 1rem;
}

div.filepicker, div.dropzone, div.dz-box, div.dz-clickable {
  padding: 0;
  background-color: rgb(180, 255, 218);
  height: 11.5rem;
  border: 0.1rem dotted rgb(130, 85, 255);
  border-radius: 1rem;
}

.dz-message {
  color: rgb(130, 85, 255);
  font-size: 1.1 rem;
  text-align: center;
}

div.filepicker-file-icon {
  margin: 3.25rem 0.5rem 2rem;
}

div.filepicker-file-icon::before {
  border: 0.2rem solid rgb(130, 85, 255);
  border-radius: 0.4rem;
  background-color: rgb(0, 255, 213);
  height: 2.8rem;
  width: 2.25rem;
}

div.filepicker-file-icon::after {
  top: 0;
  left: 0;
  border: 0.2rem solid rgb(66, 69, 74);
  width: 2.5rem;
  padding: 0.2rem 0 0.2rem;
  text-align: center;
  border-radius: 0.25rem;
  font-family: "Capriola", sans-serif;
  font-size: 0.8rem;
  color: rgb(255, 252, 217);
  background-color: rgb(130, 85, 255);
}

.portfolio-submit {
  width: 20%;
}

.portfolio-sidebar-list-wrapper .portfolio-item-thumb {
  padding: 2.1rem;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .portfolio-thumb-img img {
  width: 100%;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .title {
  color: rgb(255, 252, 217);
  font-size: 1.5rem;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions .action-icon {
  cursor: pointer;
  color: rgb(255, 215, 169);
  font-size: 1.5rem;
  transition: 0.3s ease-in-out;
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions .delete-icon:hover {
  color: rgb(255, 91, 219);
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions .clone-icon:hover {
  color: rgb(0, 255, 213);
}
.portfolio-sidebar-list-wrapper .portfolio-item-thumb .text-content .actions .edit-icon:hover {
  color: rgb(130, 85, 255);
}

.blog-container {
  display: flex;
